import React, { useState } from 'react';
import addToMailchimp from 'gatsby-plugin-mailchimp';
import { rhythm } from '../../utils/typography';

import './subscribe-email.css';

const SubscribeEmail = ({
  title = 'Enjoyed this post? Receive the next one in your inbox!'
}) => {
  const [ hasSubscribed, setHasSubscribed ] = useState(false);
  const [ hasError, setHasError ] = useState(false);
  const [ email, setEmail ] = useState('');

  const _handleSubmit = async (e) => {
    e.preventDefault();
    setHasError(false);
    const data = await addToMailchimp(email);
    console.log(data);
    if (data.result === 'error') {
      setHasError(true);
    } else if (data.result === 'success') {
      setHasSubscribed(true);
    }
  };

  return (
    <div
      className='subscribe-box'
      style={{
        padding: rhythm(2),
        marginBottom: rhythm(1),
      }}
    >
      <div style={{ fontWeight: 700, marginBottom: '10px' }}>{title}</div>
      <form
        onSubmit={_handleSubmit}
        style={{
          margin: 0
        }}
      >
        { !hasSubscribed && (
          <div
            style={{
              display: 'flex',
              justifyContent: 'center',
            }}
          >
            <input
              type='email'
              value={email}
              onChange={e => setEmail(e.target.value)}
              placeholder='Your email address'
              style={{
                border: '1px solid var(--text-normal)',
                borderRight: 0,
                borderRadius: '5px 0 0 5px',
                padding: '5px 10px',
                fontSize: '14px',
                minWidth: '200px',
                background: 'var(--bg)',
                color: 'inherit',
              }}
            />
            <button
              type='submit'
              style={{
                border: '1px solid var(--text-normal)',
                borderLeft: 0,
                borderRadius: '0 5px 5px 0',
                padding: '5px 10px',
                background: 'var(--bg)',
                WebkitAppearance: 'none',
                cursor: 'pointer',
                fontSize: '14px',
                color: 'inherit',
              }}
            >
              Subscribe
            </button>
          </div>
        ) }
        { hasSubscribed && (
          <div>
            Hooray! You're now subscribed! <span role='img' aria-label='tada'>ðŸŽ‰</span>
          </div>
        ) }
        { hasError && (
          <div
            style={{
              color: 'red',
              fontSize: '12px',
              textAlign: 'center',
            }}
          >
            There's an issue subscribing this email. Could you try another one? Or try later.
          </div>
        ) }
      </form>
    </div>
  );
};

export default SubscribeEmail;
